<template>
    <div class="teacher">

        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/school_list' }"></el-breadcrumb-item>
            <el-breadcrumb-item>teacher</el-breadcrumb-item>

        </el-breadcrumb>
        <br/>


        <el-form :inline="true" class="demo-form-inline" :loading="loading" size="mini">

            <el-form-item label="">
                <el-input v-model="search" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button-group>
                    <el-button type="primary" @click="fetch" size="mini"></el-button>
                    <el-button @click="add_form.visible=true" size="mini"></el-button>

                </el-button-group>
            </el-form-item>

        </el-form>


        <el-table
                :data="list"
                style="width: 100%"
                border
                size="mini"
                v-loading="loading"
        >


            <el-table-column label="">
                <template scope="scope">
                    {{scope.$index+(page_num-1)*page_size+1}}
                </template>
            </el-table-column>

            <el-table-column prop="name" label=""></el-table-column>
            <!--<el-table-column prop="sex" label=""></el-table-column>-->
           <el-table-column prop="mobile" label=""></el-table-column>
            <el-table-column prop="qq" label="qq"></el-table-column>





            <el-table-column label="" width="250">
                <template scope="scope">
                    <el-button-group>
                        <el-button type="danger" @click="remove(scope.row)" size="mini"></el-button>
                        <el-button @click="open_edit(scope.row)" size="mini"></el-button>
                        <el-button @click="$router.push('/teaItem?id='+scope.row.id)" size="mini"></el-button>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>


        <el-dialog title="" :visible.sync="add_form.visible"
        >
            <el-form class="demo-form-inline" label-width="100px" :model="add_form" status-icon size="mini"
                     ref="add_form">
                <el-form-item label="name" prop="">
                        <el-input v-model="add_form.name"></el-input>
                </el-form-item>
                <!--<el-form-item label="sex" prop="">-->
                        <!--<el-input v-model="add_form.sex"></el-input>-->
                <!--</el-form-item>-->
                <el-form-item label="qq" prop="qq">
                        <el-input v-model="add_form.qq"></el-input>
                </el-form-item>
                <el-form-item label="" prop="">
                        <el-input v-model="add_form.mobile"></el-input>
                </el-form-item>

            </el-form>


            <span slot="footer" class="dialog-footer">
          <el-button @click="add_form.visible=false" size="mini"> </el-button>
          <el-button type="primary" @click="add" size="mini"> </el-button>
        </span>
        </el-dialog>


        <el-dialog title="" :visible.sync="edit_form.visible"
        >
            <el-form class="demo-form-inline" label-width="100px" :model="edit_form" status-icon size="mini"
                     ref="edit_form">
              <el-form-item label="name" prop="">
                        <el-input v-model="edit_form.name"></el-input>
                </el-form-item>
                <!--<el-form-item label="sex" prop="">-->

                        <!--<el-input v-model="edit_form.sex"></el-input>-->

                <!--</el-form-item>-->
                <el-form-item label="qq" prop="qq">

                        <el-input v-model="edit_form.qq"></el-input>
                </el-form-item>
                <el-form-item label="" prop="">

                        <el-input v-model="edit_form.mobile"></el-input>

                </el-form-item>

            </el-form>


            <span slot="footer" class="dialog-footer">
          <el-button @click="edit_form.visible=false" size="mini"> </el-button>
          <el-button type="primary" @click="edit" size="mini"> </el-button>
        </span>
        </el-dialog>


        <br>
        <div style="display: inline-block;float: right">
            <el-pagination
                    background
                    layout="prev, pager, next"
                    :total="total"
                    @current-change="(p)=>page_num=p"
                    :current-page="page_num"
                    :page-size="page_size"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>


    import request from 'axios'

    export default {

        name: 'teacher',
        data() {
            return {
                list: [],
                total: 0,
                search: '',
                page_size: 10,
                page_num: 1,
                loading: false,
                add_form: {
                    visible:false,
                    name:'',
                    // sex:'',
                    mobile:'',
                    qq:''
                },
                edit_form: {
                    visible:false,
                    name:'',
                    // sex:'',
                    mobile:'',
                    qq:''
                },
            }
        },

        watch: {
            page_num() {
                this.fetch()
            }
        },
        methods: {
            change_page(p) {
                console.log(p)
            },
            open_edit(data) {
                this.edit_form = {
                    visible:true,
                    name:data.name,
                    sex:data.sex,
                    mobile:data.mobile,
                    qq:data.qq
                }
            },
            async fetch() {

                var page_size = this.page_size,
                    page_num = this.page_num,
                    search = this.search,
                    that = this
                this.loading = true
                var response = await request.get('/v1/api/teacher/', {
                    params: {
                        page_size,
                        page_num,
                        search
                    }
                })
                this.list = response.data.list
                    var page = response.data.page
                this.page_num = page.page_num
                this.page_size = page.page_size
                this.total = page.total
                this.loading = false
            },
            async add() {
                console.log(this.add_form,'add_form')
                try {

                    var add_form = this.add_form
                    var response = await request.post('/v1/api/teacher/', add_form)
                    this.add_form.visible = false
                    this.$message({
                        type: 'success',
                        message: ''
                    });
                    this.fetch()
                } catch (e) {
                    console.log(e)
                    this.$message({
                        type: 'error',
                        message: e.error
                    });
                }

            },
            async remove(data) {

                try {
                    await this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    })
                    var response = await request.delete('/v1/api/teacher/' + data.id + '/')

                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                    this.fetch()
                } catch (e) {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                }

            },
            async edit() {
                try {
                    await this.su_validate('edit_form')
                    var edit_form = this.edit_form
                    var id = edit_form.id
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                    var response = await request.put('/v1/api/teacher/' + id + '/', edit_form)
                    this.edit_form.visible = false
                    this.fetch()
                } catch (e) {
                    this.$message({
                        type: 'error',
                        message: e.error
                    });
                }
            }
        },
        mounted() {
            this.fetch()
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



